.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.6); /* dark overlay */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modal-content {
    background: #fefefe;
    border-radius: 20px;
    padding: 30px;
    max-width: 400px;
    width: 90%;
    box-shadow: 0 12px 20px rgba(0, 0, 0, 0.25);
    position: relative;
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .modal-content h2 {
    margin-top: 0;
    font-size: 28px;
    color: #333;
  }
  
  .modal-content img {
    margin: 10px 0;
  }
  
  .modal-content p,
  .modal-content li {
    margin: 8px 0;
    font-size: 16px;
    color: #444;
  }
  
  .modal-content ul {
    list-style: none;
    padding: 0;
    margin: 10px 0;
  }
  
  .modal-content strong {
    color: #222;
  }
  
  .close-button {
    position: absolute;
    top: 12px;
    right: 12px;
    font-size: 20px;
    background: #ff5e5e;
    color: white;
    border: none;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .close-button:hover {
    background: #e13e3e;
  }
  