.app-container {
  text-align: center;
  min-height: 100vh;
  padding: 20px;
  background: linear-gradient(to right, #7EC9F5, #E4F7F5);
}

.app-title {
  margin: 20px 0;
  font-size: 3.5rem;
  font-weight: bold;
  color: #222;
  text-shadow: 2px 2px 5px rgba(0,0,0,0.2);
  letter-spacing: 2px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.filter-form {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.filter-form select,
.filter-form button {
  padding: 8px 12px;
  font-size: 1rem;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.filter-form button {
  background-color: #ffc107;
  border: none;
  color: #000;
  cursor: pointer;
}

.filter-form button:hover {
  background-color: #e0a800;
}
.pagination {
  margin-top: 20px;
}

.pagination button {
  margin: 0 5px;
  padding: 6px 12px;
}

.type-normal { background: #A8A77F; }
.type-fire { background: #EE8130; }
.type-water { background: #6390F0; }
.type-electric { background: #F7D02C; }
.type-grass { background: #7AC74C; }
.type-ice { background: #96D9D6; }
.type-fighting { background: #C22E28; }
.type-poison { background: #A33EA1; }
.type-ground { background: #E2BF65; }
.type-flying { background: #A98FF3; }
.type-psychic{ background: #F95587; }
.type-bug { background: #A6B91A; }
.type-rock { background: #B6A136; }
.type-ghost { background: #735797; }
.type-dragon { background: #6F35FC; }
.type-dark { background: #705746; }
.type-steel { background: #B7B7CE; }
.type-fairy { background: #D685AD; }

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-card {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  max-width: 500px;
}
